import gsap from 'gsap';
import { ScrollTrigger } from 'gsap/all';
gsap.registerPlugin(ScrollTrigger)

var tl = gsap.timeline({
  scrollTrigger:  {
    trigger: ".video",
    start: "center center",
    pin: ".video",
    end: "+=200%",
    scrub: true
  }
});


tl.to( ".video .scroll-video", 3, {
  currentTime: 3
});


var tl2 = gsap.timeline({
  scrollTrigger:  {
    trigger: ".cards",
    start: "center center",
    pin: ".cards",
    end: "+=200%",
    scrub: true
  }
});


tl2.to( ".cards .scroll-video", 3, {
  currentTime: 5
});