window.addEventListener('scroll', function() {
  const aboutSection = document.querySelector('.about');
  const aboutBg = document.querySelector('.about__bg');

  // Получаем высоту секции .about
  const aboutHeight = aboutSection.offsetHeight;
  // Получаем расстояние от верха документа до начала секции .about
  const aboutOffsetTop = aboutSection.offsetTop;
  // Получаем текущее положение верхней границы окна просмотра
  const scrollTop = window.pageYOffset;
  // Получаем высоту окна просмотра
  const windowHeight = window.innerHeight;

  // Рассчитываем процент заполнения экрана секцией .about
  const visiblePercent = Math.max(0, Math.min(1, (windowHeight - Math.max(0, aboutOffsetTop - scrollTop)) / aboutHeight));

  if (visiblePercent == 0) {
    aboutBg.style.zIndex = -1;
    aboutBg.style.visibility = 'hidden'
  } else {
    aboutBg.style.zIndex = 2;
    aboutBg.style.visibility = 'visible'

  }
  // Применяем процент заполнения к opacity
  aboutBg.style.opacity = visiblePercent * 3;
});

window.addEventListener('scroll', function() {
  const aboutSection = document.querySelector('.price');
  const aboutBg2 = document.querySelector('.about__bg2');

  // Получаем высоту секции .about
  const aboutHeight = aboutSection.offsetHeight;
  // Получаем расстояние от верха документа до начала секции .about
  const aboutOffsetTop = aboutSection.offsetTop;
  // Получаем текущее положение верхней границы окна просмотра
  const scrollTop = window.pageYOffset;
  // Получаем высоту окна просмотра
  const windowHeight = window.innerHeight;

  // Рассчитываем процент заполнения экрана секцией .about
  const visiblePercent2 = Math.max(0, Math.min(1, (windowHeight - Math.max(0, aboutOffsetTop - scrollTop)) / aboutHeight));
  if (visiblePercent2 == 1) {
    aboutBg2.style.zIndex = -1;
    aboutBg2.style.visibility = 'hidden'
  } else {
    aboutBg2.style.zIndex = 2;
    aboutBg2.style.visibility = 'visible'

  }
  // Применяем процент заполнения к opacity
  aboutBg2.style.opacity = 1 - visiblePercent2;
  aboutBg2.style.zIndex = 1 - visiblePercent2 * 2;
});